import React from "react";
import { NavLink } from "react-router-dom";

const Header = () => {
  const show = () => {
    alert(
      "Pomodoro timer is a useful technique used for improving your focus."
    );
  };
  return (
    <div className="navigation">
      {/* <p className="navigation__header">Simple Pomodoro Timer</p> */}
      <NavLink activeClassName="selected" exact to="/pomodoro" className="btn btn--1 btn--nav">
        Home
      </NavLink>
      {/* <NavLink to="/pomodoro" exact activeClassName="selected" className="btn btn--2 btn--nav">
        What is this?
      </NavLink> */}
      <NavLink activeClassName="selected" exact to="/pomodoro/how" className="btn btn--3 btn--nav">
        How to use
      </NavLink>
    </div>
  );
};

export default Header;



