import React, { Component } from "react";

export default class How extends Component {
  state = {};
  render() {
    return (
      <div className="how">
        <p className="how__paragraph">
          Pomodoro Timer is a technique used for improving focus. The basic idea
          of it is you will work for X minutes, then take a short break of Y
          minutes.
        </p>
        <p className="how__paragraph">
          For example, in this website, the default is you work for 25 minutes,
          then you can take short break of 5 minutes.
        </p>
        <p className="how__paragraph">
          To start, set your ideal interval for each working session.
        </p>
        <p className="how__paragraph">
        Then, set you ideal interval for each break session.</p>
        <p className="how__paragraph">
        Finally, set how many sessions you want to run for this time!</p>
      </div>
    );
  }
}



